*{
    box-sizing: border-box;
    font-family: Arial, Helvetica, sans-serif;
}

body{
    background: url('./img/bg.jpg');
    background-size: cover;
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-position: center;
    margin: 0;
    overflow: overlay;
}

::-webkit-scrollbar{
    background: transparent;
}

::-webkit-scrollbar-thumb{
    background-color: #45505b;
    border: 3px solid transparent;
    border-radius: 9px;
    background-clip: content-box;
}

::-webkit-scrollbar-corner{
    border-radius: 10px;
}

header {
    height: 100%;
    position: fixed;
    /* top: 0; */
    left: 0;
    bottom: 0;
    z-index: 9997;
    transition: all 0.5s;
    overflow-y: auto;
    justify-content: center !important;
    flex-direction: column !important;
    display: flex !important;
}

.nav-menu{
    padding: 0;
    display: block;
    position: relative;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    padding-top:1rem;
    padding-bottom: 1rem;
    margin-left: 20px;
    padding: 0;
}

ul{
    display: block;
    margin-block-start: 1em;
    margin-block-end: 1em;
    margin-inline-start: 0px;
    margin-inline-end: 0px;
    padding-inline-start: 40px;
    margin: 0;
    padding: 0;
}

.nav-menu>ul>li {
    position: relative;
    white-space: nowrap;
    display: list-item;
    text-align: -webkit-match-parent;
    list-style: none;
}

.nav-menu>ul>li.active a{
    color: #66c691;
    background-color: #45505b;
}

li{
    list-style: none;
}

.nav-menu>ul>li a{
    display: flex;
    align-items: center;
    color: #45505b;
    padding: 10px 18px;
    margin-bottom: 8px;
    transition: 0.3s;
    font-size: 20px;
    font-weight: bolder;
    border-radius: 50px;
    background: #f2f3f5;
    height: 56px;
    width: 56px;
    overflow: hidden;
    transition: 0.1s;
}

.nav-menu>ul>li a:hover {
    color: #66c691;
    height: 66px;
    width: 66px;
    font-size: 30px;
}



.nav-menu a i,
.nav-menu a:focus i {
    font-size: 20px;
}

.container{
    margin: 100px;
    color: #fff;
}

.hero{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-70%, -50%);
    margin: 0;
    width: 50%;
}

.hero h1{
    font-size: 65px;
    margin-top: 0;
    margin-bottom: 5px;
}

.hero h3{
    font-size: 42px;
    margin-top: 0;
    margin-bottom: 5px;
}

.hero span{
    font-size: 26px;
    margin-top: 15px;
    color: #66c691;
}

.photo {
    width: 50%;
}

.photo img{
    width: 500px;
    bottom: 0px;
    right: 60px;
    position: fixed;
}

.about {
    margin-left: 100px;
    width: 50%;
    position: relative;
    z-index: 1;
}

.about h1 {
    color: #fff;
    font-size: 50px;
    line-height: 15px;
}

.about h3 {
    color: #fff;
    font-size: 30px;
    line-height: 15px;
    word-wrap: break-word;
    margin-bottom: 5px;
    padding-bottom: 15px;
    border-bottom: 3px solid #66c691;
}

.about p {
    color: #fff;
    font-size: 18px;
    word-wrap: break-word;
}

.about span {
    color: #66c691;
}

.about ul {
    font-size: 18px;
    line-height: 32px;
}

.about label {
    display: block;
    font-size: 20px;
    line-height: 40px;
}

.skill-warp {
    display: flex;
    width: 100%;
    position: relative;
    z-index: 1;
}

.skill {
    width: 50%;
}

.skill label {
    font-size: 18px;
}

progress {
    display: block;
    border: none;
    width: 80%;
    height: 10px;
    background: #fff;
}

progress {
    color: lightblue;
}

progress::-moz-progress-bar {
    background: lightcolor;
}

progress::-webkit-progress-value {
    background: #66c691;
}

progress::-webkit-progress-bar {
    background: #fff;
}

.experience {
    margin-left: 100px;
    width: 50%;
    position: relative;
    z-index: 1;
}

.experience h1 {
    color: #fff;
    font-size: 50px;
    line-height: 15px;
}

.experience h3 {
    color: #fff;
    font-size: 24px;
    line-height: 15px;
    word-wrap: break-word;
    margin-bottom: 5px;
    padding-bottom: 10px;
    border-bottom: 3px solid #66c691;
}

.experience span {
    margin-top: 12px;
    display: block;
    color: #66c691;
    font-size: 18px;
    word-wrap: break-word;
    margin-bottom: 0px;
}

.experience p {
    color: #fff;
    font-size: 18px;
    word-wrap: break-word;
    margin-bottom: 0px;
}

.experience span {
    color: #66c691;
}

@media screen and (max-width:850px) {
    .photo{
        width: 100%;
    }

    .photo img {
        width: 500px;
        bottom: 0px;
        right: 0;
        position: fixed;
        margin: auto;
        left: 0;
    }

    .hero {
        position: relative;
        text-align: center;
        padding-top: 30%;
        left: 50%;
        transform: translate(-50%, -50%);
        margin: 0;
        width: 100%;
    }

    .about {
        position: relative;
        text-align: center;
        left: 0;
        margin: 0;
        width: 100%;
    }

    .about label {
        display: block;
        line-height: 40px;
        text-align: left;
    }

    progress {
       width: 90%;
    }

    .experience {
        position: relative;
        text-align: center;
        left: 0;
        margin: 0;
        width: 100%;
    }

}

@media screen and (max-width:540px) {
    .container{
        margin:50px 50px 100px 50px;
    }

    header {
        width: 100%;
        height: 80px;
        position: fixed;
        left: 0;
        bottom: 15px;
        margin: auto;
    }

    .nav-menu {
        margin: auto;
    }

    ul {
        display: inline-block;
        margin: auto;
        padding: 0;
    }

    .nav-menu>ul>li {
        position: relative;
        white-space: nowrap;
        display: inline-block;
        text-align: -webkit-match-parent;
        list-style: none;
        padding: 2px;
    }

    .nav-menu>ul>li a {
        height: 50px;
        width: 50px;
    }

    .photo {
        width: 100%;
    }

    .photo img {
        width: 350px;
        bottom: 0px;
        right: 0;
        position: fixed;
        margin: auto;
        left: 0;
    }

    .hero h1 {
        font-size: 35px;
        margin-top: 0;
        margin-bottom: 5px;
    }

    .hero h3 {
        font-size: 22px;
        margin-top: 0;
        margin-bottom: 5px;
    }

    .hero span {
        font-size: 16px;
        margin-top: 15px;
        color: #66c691;
    }

    .about h1 {
        font-size: 35px;
        margin-top: 0;
        margin-bottom: 10px;
    }

    .about h3 {
        font-size: 22px;
        margin-top: 0;
        margin-bottom: 5px;
    }

    .about span {
        font-size: 16px;
        margin-top: 15px;
        color: #66c691;
    }

    .about label {
        font-size: 11px;
    }


    .experience{
        text-align: left;
    }

    .experience h1 {
        font-size: 35px;
        margin-top: 0;
        margin-bottom: 10px;
        line-height: 35px;
    }

    .experience h3 {
        font-size: 22px;
        margin-top: 0;
        margin-bottom: 5px;
        line-height: 22px;
    }

    .experience p {
        font-size: 16px;
        margin-top: 15px;
        color: #fff;
        border-bottom: 3px solid #66c691;
        margin-bottom: 15px;
    }

    
}